.objectId;\r\n                var userType = store.state.player.currentPlayer.userType;\r\n\r\n                if(userId && userType)\r\n                    transition.redirect({name: 'home'});\r\n                else if(userId)\r\n                    transition.redirect({name: 'profile'});\r\n                else\r\n                    transition.next();\r\n            }\r\n        }\r\n    };\r\n</script>"],"sourceRoot":"webpack://"}]);

